function expired() {
  return (
    <div><br></br>
      <p>Page expired.</p>
    </div>
  );
}

function CoverLetter(){


  let yesterday = new Date(2020, 2, 8);
  let today = new Date();
  let diff =  Math.floor((today/86400000)-(yesterday/86400000));
  let when = () => {
    if (diff < 45){
      return unexpired();
    } else {
      return expired();
    }
  };

  return (
    <div className='coverLetterDiv'>
      {when()}

      <style>{`
      .coverLetterDiv {
        position: relative;
        top: -2vh;
        background-color: rgba(245, 255, 245, .66);
        border-radius: 25px;
        min-height: 88vh;
        padding-left: 10%;
        padding-right: 10%;

      }
      .coverLetterP {
        text-align: left !important;
      }
      .coverLetterHR{
        color: green;
        border: 3px ridge;
      }
      `}
      </style>
    </div>
  );
}


export default CoverLetter;
